"use trist"
// Загрузка контента
window.onload  = function() {

    const parallax = document.querySelector('.haeserMain');

    if(parallax) {
        
        const content = document.querySelector('.content');
        const parallax_avto = document.querySelector('.avto');
        const parallax_dust = document.querySelector('.dust');

        //Коэфициенты
        const for_content = 40;
        const for_parallax_avto = 20;
        const for_parallax_dust = 10;

        // Скорость анимации
        const speed = 0.05;

        //Обьявление переменных
        let positionX = 0, positionY = 0;
        let coordXprocent = 0, coordYprocent = 0;

        //Анимация
        function setMouseParallaxStyle() {
            const distX = coordXprocent - positionX;
            const distY = coordYprocent - positionY;

            positionX = positionX + (distX * speed);
            positionY = positionY + (distY * speed);

            //Передача элементу стилей 
            content.style.cssText = `transform : translate(${positionX / for_content}%, ${positionY / for_content}%);`;
            parallax_avto.style.cssText = `transform : translate(${positionX / for_parallax_avto}%, ${positionY / for_parallax_avto}%);`;
            parallax_dust.style.cssText = `transform : translate(${positionX / for_parallax_dust}%, ${positionY / for_parallax_dust}%);`;

            requestAnimationFrame(setMouseParallaxStyle);
        }
        setMouseParallaxStyle();

        parallax.addEventListener("mousemove", function(e) {
            //Получение ширины и высоты блока
            const parallaxWidth = parallax.offsetWidth;
            const parallaxHeight = parallax.offsetHeight;

            //Ноль по середине
            const coordX = e.pageX - parallaxWidth / 2;
            const coordY = e.pageY - parallaxHeight / 2;

            coordXprocent = coordX / parallaxWidth * 100;
            coordYprocent = coordY / parallaxHeight * 100;
        });

        // //Scroll
        // let thresholdSets = [];
        // for(let i = 0; i <= 1.0; i += 0.005) {
        //     thresholdSets.push(i);
        // }
        // const callback = function(entries, observer) {
        //     const scrollTopProcent = window.pageXOffset / parallax.offsetHeight * 100;
        //     setMouseParallaxStyle(scrollTopProcent);
        // }

        // const observer = new IntersectionObserver(callback, {
        //     threshold : thresholdSets
        // })

        // observer.observe(document.querySelector('.content'));

        // function setParallaxItemsStyle(scrollTopProcent) {
        //     content.style.cssText = `transform : translate(0%, - ${scrollTopProcent / 9}%);`;
        //     parallax_avto.parentElement.cssText = `transform : translate(0%, - ${scrollTopProcent / 6 }%);`;
        //     parallax_dust.parentElement.cssText = `transform : translate(0%, - ${scrollTopProcent / 3 }%);`;

        // }

    }
}